.projects{
    width:95%;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:row;
    flex-wrap:wrap;
    height:auto;
    padding:35px;
 
    background: #080c1f;
    display:grid;
    grid-template-columns:repeat(4,auto)
}
.section{
display:grid;
grid-template-columns: repeat(4,auto);

}
/* white ish = #dfe7eb; */
.projectImg{
    width:250px;
    margin-right:20px;
    cursor:pointer;
    border-radius:10px;
    transition:0.6s all ease;
    margin-bottom:30px;
}
.projectImg:hover{
    filter:brightness(0.5);
    transform:scale(1.1)
}
@media (max-width:1200px) {
.projectImg{
        width:230px
    }
}
@media (max-width:1087px) {
.projectImg{
        width:220px
    }
}

@media (max-width:1024px) {
    .projectImg{
        width:210px
    }
}
@media (max-width:1010px){
    .section{
        display:grid;
        grid-template-columns: repeat(3,auto);
        
        }
}
@media (max-width:810px){
    .section{
        position:relative;
        right:30px;
    }
.projectImg{
        width:200px
    }
}
@media (max-width:720px){
.projectImg {
        width:190px
    }
}

@media (max-width:700px){
    .section{
        display:grid;
        grid-template-columns: repeat(2,auto);
    }
}
  @media (max-width:500px){
    .projectImg {
          width:180px
      }
  }
  @media (max-width:427px){

.projectImg { 
    width:150px;
}
}